*{
  box-sizing: border-box;
  ;
  
}
.container--reg{
  display:flex;  
  flex-direction: column;
  justify-content: center;
  align-items: center;
 
  width: 700px;
  height: 700px;
  background:#f6f7f8;
  color:rgb(6, 6, 6);
  margin: 100px 700px;
  border-radius: 10px;


    

}
input{
    border-radius:5px;
    font-weight: 500;
    width:280px;
    height: 56px;
    padding: 0 16px;
    border-radius: 8px;
    justify-content: center;
    text-align: center;
    font-weight: 900;
}
#btn{
  display: flex;
  padding:10px;
  margin:20px 65px;
  width: 150px;
  
  border: none;
  border-radius: 2px;
  background: #030303;
  color:rgb(255, 255, 255);
  text-decoration: solid;
  font-size: 18px;
  font-weight: 600;
  justify-content: center;
  align-items: center;
  line-height: 20px;
  border-radius: 5px;
  box-shadow: 0px 2.98256px 7.4564px rgb(165, 218, 227);
}
.lbl{
    padding:5px;
    margin:5px;
    font-weight: bold;
}
#head2{
  display:flex;
    align-items: center;
    justify-content: center;
    padding: 0px;
    margin: 0px;
}
#head5{
  display:flex;
  justify-content: center;
  align-items: center;
}
input,label{
    flex-direction: row;
}
.form{
    display: grid;
    grid-column-end: auto;
    grid-column: auto;
    align-items: center;
    justify-content: center;
}
