.navcontainer{
    display: block;
}
.container{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    box-shadow: 0px 2.98256px 7.4564px rgb(165, 218, 227);
    position:fixed;
    width: 100%;
    height:70px;
    background-color:#f5f0f0;
    margin:0px 0px 0px 0px;
    left: 0px;
    color:rgb(3, 24, 42);
   
    
  

}
.logo{
    font-size: 22px;
    margin-left: 15px;
    padding: 3px 3px;
    display: inline;
    margin-top: 15px;
}
ul{
    display:flex;
    flex-direction: row;
    list-style-type: none;
    align-items: center;
    justify-content: space-around
}
.item{
    padding: 1em;
    display:flex;
    justify-content:space-between;
    align-items: center;
    text-decoration: none;
    font-size: 1emem;
    font-family: 'Jost', sans-serif;
    text-decoration-line: none;
     font: weight 60px; ;


}
.container button{
    width: 60px;
    height: 35px;
    margin-top: 15px;
    font-size: 21px;
    background-color: white;
    border: none;
}

a{
    text-decoration: none;
    font-size: 18px;
    color:rgb(3, 24, 42);
}
.search-bar{
    width:400px;
    height:35px;
    align-items: center;
    margin:16px;
    
    border-style: none;
    box-shadow: 1px 1px 8px 2px black;
    

    



}

.searchbar{
    display :flex;
    gap:0 0;
}
.hammenu div{
        display: none;
        width: 35px;
        height: 5px;
        background-color: black;
        margin: 6px 0;
      
}
.hammenu{
    float: right;
    margin-left: 170px;
    margin-top: 15px;

}
@media only screen and (max-width:1060px) {
    .container ul{
        display: none;
    }
    .logo{
        display: inline;
        width: 200px;
    
    }
    .hammenu div{
        display: block;
    }

    .hammenu{
        display: block;
        float: right;
    }
}
@media only screen and (max-width:700px) {
.search-bar{
    width: min-content;

}
.container button{
    font-size: 17px;
}

}
@media only screen and (max-width:400px) {
    .search-bar{
        width: 150px;
    }
}