.big-container{
    display:flex;
    height: 600px;
    width: 800px;
    top: 100px;
    left: 200px;
    position :sticky;


}
.logincontainer{
    display: block;
    width: 350px;
    height: 500px;
    background-color: rgb(246, 247, 248);
    position :sticky;
  
    float:left
}
.logincontainer p{
    font-size: 35px;
    justify-content: center;
    font-weight: bold;
    text-align: center;
    padding-top:50px ;
    color: black;
    
}
.userpass input{
    margin: 0px 0px 0px 26px;
    text-decoration: none;
    border: none;
    display: inline;
    background-color: rgb(246, 247, 248);
    color: black;
    outline: none;


}
.userpass hr{
    width: 300px;
    height: 3px;
    background-color: black;
    border: none;
}
.logn button{
    width: 120px;
    height: 45px;
    margin: 20px 20px 20px 20px;
   
    
    border-radius: 8px;
}
#pword{
    margin-top: 35px;
}
.btn{
    border: 2px solid black;
    color:black
}
#btn2{
    background-color: black;
    color:aliceblue;
}
.btn:hover{
    background-color: black;
    color:aliceblue;
}
#btn2:hover{
    border: 2px solid black;
    color:black;
    background-color: white;
}






